  const updateEventData = (eventJson) => {
    var count = Object.keys(eventJson).length;
    if (count > 0) {
      var Events = {};
      Events["Events"] = eventJson;
      props.selectedControl["event"] = Events;
    }
    updatedComponentInFormState(
      props.selectedControl.id,
      props.selectedControl
    );
  };
  //Function to Validate the Min and Max Date
  function compareDates(date1, dateformat1, date2, dateformat2) {
    if (date1 == 0 || date2 == 0) {
      return -1;
    } else if (date1 > date2) {
      return 1;
    } else if (date1 == date2) {
      return -1;
    }
    return 0;
  }
  function validateDate(evt, propertyName, propertyValue) {
    var today = new Date();
    var dd = today.getDate();
    var mm = today.getMonth() + 1;
    var yyyy = today.getFullYear();
    if (dd < 10) {
      dd = "0" + dd;
    }
    if (mm < 10) {
      mm = "0" + mm;
    }
    var dateText = propertyValue;
    var currDate = yyyy + "-" + mm + "-" + dd;
    var todayDate = currDate;
    var maxDateValue = props.selectedControl.style["MaxDate"];
    var minDateValue = props.selectedControl.style["MinDate"];
    var mincurrdateselected = props.selectedControl.style["MinDateAsCurrent"];
    var maxcurrdateselected = props.selectedControl.style["MaxDateAsCurrent"];
    if (propertyName === "MinDate" && maxcurrdateselected === "false")
      currDate = maxDateValue;
    else if (propertyName === "MaxDate" && mincurrdateselected === "false")
      currDate = minDateValue;
    if (propertyName === "MinDateAsCurrent" && propertyValue === "true") {
      dateText = currDate;
      currDate = maxDateValue;
      handleProperty("MinDate", "");
    } else if (propertyName === "MinDateAsCurrent" && propertyValue === "false") {
      dateText = "";
    }
    if (propertyName === "MaxDateAsCurrent" && propertyValue === "true") {
      dateText = currDate;
      currDate = minDateValue;
      handleProperty("MaxDate", "");
    } else if (propertyName === "MaxDateAsCurrent" && propertyValue === "false") {
      dateText = "";
    }

    let validated = true;
    switch (compareDates(currDate, "dd/MM/yyyy", dateText, "dd/MM/yyyy")) {
      case 0: // greater than current date
        if (propertyName === "MinDate" || propertyName === "MinDateAsCurrent") {
          validated = false;
          setAlertData({
            ...ControlsMap["customizedAlertNotificationData"],
            open: true,
            alertContentKey: "MIN_DATE_CAN_NOT_BE_GREATER",
          });
          setMinDateClear(true);
          window.event.preventDefault();
        }
        break;
      case 1: // less than current date
        if (propertyName === "MaxDate" || propertyName === "MaxDateAsCurrent") {
          validated = false;
          setAlertData({
            ...ControlsMap["customizedAlertNotificationData"],
            open: true,
            alertContentKey: "MAX_DATE_CAN_NOT_BE_LESS",
          });
          setMaxDateClear(true);
          window.event.preventDefault();
        }
        break;
      default: 
        setHelperText(null);
        break;
    }
    if(validated){
      handleProperty(propertyName, propertyValue);
      if (propertyName === "MinDateAsCurrent" && propertyValue === "true") {
        handleProperty("MinDate", todayDate);
      }
      if (propertyName === "MaxDateAsCurrent" && propertyValue === "true") {
        handleProperty("MaxDate", todayDate);
      }
      
    } else {
      if (propertyName === "MinDate" || propertyName === "MaxDate")
        handleProperty(propertyName, '');
    }
  }
  function selectedOptionForTab(
    selectedControlId,
    selectedControlLabel,
    ControlLabelKey
  ) {
    var select = document.getElementById(selectedControlId + ControlLabelKey);
    var option_tag = select.options[select.selectedIndex];

    let temp_style = props.selectedControl.style;
    temp_style[ControlLabelKey] = option_tag.value;
    props.selectedControl.style[ControlLabelKey] = option_tag.value;

    
  
    if(ControlLabelKey?.includes("Label")){
      if (option_tag.value === "Regular Italic") {
        props.selectedControl.style[ControlLabelKey] = 300;
        props.selectedControl.style["LabelFontStyle"] = "italic";
        props.selectedControl.style["LabelWeightStyle"] = option_tag.value;
      } else if (option_tag.value === "Semibold" || option_tag.value === "Medium") {
        props.selectedControl.style[ControlLabelKey] = 600;
        props.selectedControl.style["LabelFontStyle"] = "normal";
        props.selectedControl.style["LabelWeightStyle"] = option_tag.value;
      } else if (option_tag.value === "Bold") {
        props.selectedControl.style[ControlLabelKey] = "bold";
        props.selectedControl.style["LabelFontStyle"] = "normal";
        props.selectedControl.style["LabelWeightStyle"] = option_tag.value;
      } else if (option_tag.value === "Regular") {
        props.selectedControl.style[ControlLabelKey] = 300;
        props.selectedControl.style["LabelFontStyle"] = "normal";
        props.selectedControl.style["LabelWeightStyle"] = option_tag.value;
      } else if (option_tag.value === "Bold Italic") {
        props.selectedControl.style[ControlLabelKey] = "bold";
        props.selectedControl.style["LabelFontStyle"] = "italic";
        props.selectedControl.style["LabelWeightStyle"] = option_tag.value;
      } else if (option_tag.value === "Light Italic") {
        props.selectedControl.style[ControlLabelKey] = 100;
        props.selectedControl.style["LabelFontStyle"] = "italic";
        props.selectedControl.style["LabelWeightStyle"] = option_tag.value;
      } else if (option_tag.value === "Semibold Italic") {
        props.selectedControl.style[ControlLabelKey] = 600;
        props.selectedControl.style["LabelFontStyle"] = "italic";
        props.selectedControl.style["LabelWeightStyle"] = option_tag.value;
      } else if (option_tag.value === "Light") {
        props.selectedControl.style[ControlLabelKey] = 100;
        props.selectedControl.style["LabelFontStyle"] = "normal";
        props.selectedControl.style["LabelWeightStyle"] = option_tag.value;
      }
    }
    else{
      if (option_tag.value === "Regular Italic") {
        props.selectedControl.style[ControlLabelKey] = 300;
        props.selectedControl.style["FontStyle"] = "italic";
        props.selectedControl.style["WeightStyle"] = option_tag.value;
      } else if (option_tag.value === "Semibold" || option_tag.value === "Medium") {
        props.selectedControl.style[ControlLabelKey] = 600;
        props.selectedControl.style["FontStyle"] = "normal";
        props.selectedControl.style["WeightStyle"] = option_tag.value;
      } else if (option_tag.value === "Bold") {
        props.selectedControl.style[ControlLabelKey] = "bold";
        props.selectedControl.style["FontStyle"] = "normal";
        props.selectedControl.style["WeightStyle"] = option_tag.value;
      } else if (option_tag.value === "Regular") {
        props.selectedControl.style[ControlLabelKey] = 300;
        props.selectedControl.style["FontStyle"] = "normal";
        props.selectedControl.style["WeightStyle"] = option_tag.value;
      } else if (option_tag.value === "Bold Italic") {
        props.selectedControl.style[ControlLabelKey] = "bold";
        props.selectedControl.style["FontStyle"] = "italic";
        props.selectedControl.style["WeightStyle"] = option_tag.value;
      } else if (option_tag.value === "Light Italic") {
        props.selectedControl.style[ControlLabelKey] = 100;
        props.selectedControl.style["FontStyle"] = "italic";
        props.selectedControl.style["WeightStyle"] = option_tag.value;
      } else if (option_tag.value === "Semibold Italic") {
        props.selectedControl.style[ControlLabelKey] = 600;
        props.selectedControl.style["FontStyle"] = "italic";
        props.selectedControl.style["WeightStyle"] = option_tag.value;
      } else if (option_tag.value === "Light") {
        props.selectedControl.style[ControlLabelKey] = 100;
        props.selectedControl.style["FontStyle"] = "normal";
        props.selectedControl.style["WeightStyle"] = option_tag.value;
      }
    }
  
    updatedComponentInFormState(
      props.selectedControl.id,
      props.selectedControl
    );
  }


  function handleToggle(e, field) {
    const value = e.target.value;
    if(field === "Enable") {
      props.selectedControl.style["previousEnable"] = value;
    }
    if(field === "ReadOnlyStyle") {
      props.selectedControl.style["previousReadOnly"] = value;
    }
    if(field === "ReadOnly") {
      props.selectedControl.style["previousReadOnlyValue"] = value;
    }
    props.selectedControl.style[field] = value;
    updatedComponentInFormState(
      props.selectedControl.id,
      props.selectedControl
    );
  }

